







import { Vue, Component, Prop } from 'vue-property-decorator'

@Component({
  name: 'VuiIcon'
})
export default class VComponent extends Vue {
  // 图标名称
  @Prop({
    type: String,
    required: true
  })
  readonly name!: string

  // 是否禁用
  @Prop({
    type: Boolean,
    default: false
  })
  readonly disabled!: boolean
}
